/* 전체 앱 스타일 */
.app {
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 20px;
  font-family: 'Arial', sans-serif;
}

.app-container {
  max-width: 600px;
  margin: 0 auto;
  background: white;
  border-radius: 15px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  padding: 30px;
}

.app-title {
  text-align: center;
  color: #333;
  margin-bottom: 30px;
  font-size: 2.5rem;
  font-weight: bold;
}

/* TodoForm 스타일 */
.todo-form {
  margin-bottom: 30px;
}

.input-container {
  display: flex;
  gap: 10px;
  align-items: center;
}

.todo-input {
  flex: 1;
  padding: 15px;
  border: 2px solid #e1e1e1;
  border-radius: 8px;
  font-size: 16px;
  outline: none;
  transition: border-color 0.3s ease;
}

.todo-input:focus {
  border-color: #667eea;
}

.todo-input::placeholder {
  color: #999;
}

.add-button {
  padding: 15px 25px;
  background: #667eea;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  min-width: 80px;
}

.add-button:hover:not(:disabled) {
  background: #5a67d8;
  transform: translateY(-2px);
}

.add-button:disabled {
  background: #ccc;
  cursor: not-allowed;
  transform: none;
}

/* TodoList 스타일 */
.todo-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

/* TodoItem 스타일 */
.todo-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  background: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 8px;
  transition: all 0.3s ease;
}

.todo-item:hover {
  background: #e9ecef;
  transform: translateX(5px);
}

.todo-item.completed {
  background: #d4edda;
  border-color: #c3e6cb;
}

.todo-content {
  display: flex;
  align-items: center;
  gap: 15px;
  flex: 1;
}

.todo-checkbox {
  width: 20px;
  height: 20px;
  cursor: pointer;
}

.todo-text {
  font-size: 16px;
  color: #333;
  word-break: break-word;
}

.todo-text.completed-text {
  text-decoration: line-through;
  color: #6c757d;
  opacity: 0.7;
}

.delete-button {
  background: none;
  border: none;
  font-size: 20px;
  cursor: pointer;
  padding: 5px;
  border-radius: 4px;
  transition: all 0.3s ease;
}

.delete-button:hover {
  background: #ff4757;
  transform: scale(1.1);
}

/* 빈 상태 메시지 */
.empty-message {
  text-align: center;
  color: #6c757d;
  font-style: italic;
  margin-top: 30px;
  padding: 20px;
  background: #f8f9fa;
  border-radius: 8px;
  border: 2px dashed #dee2e6;
}

/* 반응형 디자인 */
@media (max-width: 768px) {
  .app {
    padding: 10px;
  }
  
  .app-container {
    padding: 20px;
  }
  
  .app-title {
    font-size: 2rem;
  }
  
  .input-container {
    flex-direction: column;
  }
  
  .todo-input {
    width: 100%;
    margin-bottom: 10px;
  }
  
  .add-button {
    width: 100%;
  }
  
  .todo-item {
    padding: 12px;
  }
  
  .todo-item:hover {
    transform: none;
  }
}

@media (max-width: 480px) {
  .app-title {
    font-size: 1.8rem;
  }
  
  .todo-text {
    font-size: 14px;
  }
}